body {
  font-family: "Bitter", Georgia, "Times New Roman", Times, serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Luckiest Guy", Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
}

hero {
  display: block;
  position: relative;
  background-image: linear-gradient(#2B6869, #D2CFCE);
}

.hero-background {
  z-index: 1;
}

.hero-logo {
  z-index: 2;
  position: absolute;
  top: 0;
}

.hero-foreground {
  position: absolute;
  width: 100%;
  top: 0;
  z-index: 4;
}

.hero-squabs {
  z-index: 5;
  position: absolute;
  bottom: -186px;
  width: 900px;
  left: 50%;
  transform: translateX(-50%);
}
@screen 2xl {
  .hero-squabs {
    bottom: 102px;
  }
}

section#park {
  margin-top: 247px;
}